*{margin:0;padding:0;font-family:"Roboto",sans-serif}.todoBoard{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;height:100vh}.form_add__container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.form_add{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:570px;height:10rem}.form_add__input{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.todos{display:-webkit-flex;display:flex;-webkit-flex-flow:wrap row;flex-flow:wrap row;-webkit-justify-content:center;justify-content:center;width:98%;max-width:1200px;margin:auto;margin-top:0;margin-left:0;margin-right:0;padding:1.2rem 0 2rem}.todo{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;margin:auto}@media screen and (min-width: 690px){.todos{display:-webkit-flex;display:flex;-webkit-flex-flow:wrap row;flex-flow:wrap row;-webkit-justify-content:center;justify-content:center;width:85%;max-width:1200px;margin:auto;margin-top:2rem;padding:1.2rem 0 2rem}}.form_add{padding:1rem;box-shadow:5px 2px 5px 2px rgba(0,0,0,.34);background-image:url(https://cdn.wallpapersafari.com/58/81/UMWQ1d.jpg);background-repeat:no-repeat;background-size:100% 100%}.form_add__input{padding:2rem .5 1rem;margin-left:1rem}.form_add__input__title{border:none;border-bottom:1px solid #fff;color:#fff;background-color:transparent;margin-left:2rem}.form_add__input__desc{border:none;border-bottom:1px solid #fff;color:#fff;background-color:transparent;margin-left:2rem}.form_add__btn{border:none;background:transparent;max-width:2.5rem;max-height:2.5rem;margin:2rem 1rem 1rem 1.5rem;padding:0;color:#fff}.form_add__btn__icon{font-size:1.5rem;cursor:pointer;color:#fff;transition:.2s linear}.form_add__btn__icon:hover{color:green}.form_add__label{margin-top:1.5rem;margin-bottom:.3rem;color:#fff;text-decoration:underline}.todos{border:double rgba(189,187,187,.553);background-color:#fff;background-image:linear-gradient(#eee 0.1em, transparent 0.1em);background-size:100% 1.2em}.todo{width:95%;max-width:480px;background-color:transparent;transition:linear .3s;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-top:.75rem;margin-bottom:.5rem;cursor:pointer}.todo:hover{width:96%;background-color:#f5f5f5}.todo--done{background-color:rgba(128,128,128,.361);box-shadow:inset 5px 2px 5px 2px rgba(0,0,0,.34);transition:linear .3s;border:none;-webkit-order:1;order:1}.todo--done:hover{width:95%;background-color:rgba(128,128,128,.183)}.todo,article,.todo__btn{-webkit-flex-basis:90%;flex-basis:90%}.todo__article{-webkit-flex-shrink:1;flex-shrink:1;border-left:solid 10px green;transition:linear .05s}.todo__article--done{border-left:solid 0 green;transition:linear .05s}.todo__article__text{margin:0 .5rem .5rem}.todo__article__text__title{text-decoration:underline;margin:.5rem .5rem 0}.todo__article__text--done{text-decoration:line-through;color:gray}.todo__btn{-webkit-flex-shrink:30;flex-shrink:30;opacity:0;transition:linear .05s;border:none;cursor:pointer}.todo__btn--done{width:0px;opacity:1;-webkit-flex-shrink:20;flex-shrink:20;transition:linear .3s;background-color:red;color:#fff}.todo__btn--done:hover{-webkit-flex-shrink:10;flex-shrink:10}.todo__btn__icon{display:none;font-size:1.2rem;padding-top:.5rem}.todo__btn__icon--done{display:inline}.footer{margin-top:2rem;font-size:2rem;transition:.02s linear;text-align:center;text-decoration:none;color:#000}.footer__link:visited{color:#000}.footer__icon{cursor:pointer}.footer__icon:hover{font-size:2.1rem}
